.header {
  background-image: url(./../images/header-bg.png);
  background-size: cover;
  background-position-x: 85%;

  @include onTablet {
    background-position: center;
  }

  &__content {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    text-align: center;
    font-size: 36px;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 64px;
    }
  }

  &__bottomn {
    padding-bottom: 88px;

    @include onTablet {
      padding-bottom: 130px;
    }
  }

}
